.switch-box {
  overflow: hidden;
  color: #fff;
  cursor: pointer;
  margin-bottom: 10px;
  float: left;
  margin-right: 10px;
}

.switch-label, .switch-btn {
  height: 25px;
  line-height: 25px;
  float: left;
}

.switch-label {
  padding-right: 5px;
}

.switch-btn {
  position: relative;
  width: 55px;
  background-color: #ffffff5e;
  border-radius: 26px;
}

.switch-btn::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  height: 22px;
  width: 22px;
  border-radius: 50px;
  background-color: #fff;
  -webkit-transition: left .3s;
  transition: left .3s;
}

.switch-box.active .switch-btn::before {
  left: 30px;
}

.switch-box.active .switch-btn {
  background-color: #FF9800;
}

